#debungcontrolls {
  z-index: 100000;
  position: fixed;
  bottom: 0px;
  width: 100%;
  height: auto;
  background: rgba(0, 0, 0, 0.6);
  padding: 10px;
  box-sizing: border-box;
}

.debugtimeline {
  width: 100%;
  height: 10px;
  position: relative;
  display: block;
  margin-bottom: 3px;
  display: none;
  white-space: nowrap;
  box-sizing: border-box;
}

.debugtimeline:hover {
  height: 15px;
}

.the_timeline_tester {
  background: #e74c3c;
  position: absolute;
  top: 0px;
  left: 0px;
  height: 100%;
  width: 0;
}

.rs-go-fullscreen {
  position: fixed !important;
  width: 100% !important;
  height: 100% !important;
  top: 0px !important;
  left: 0px !important;
  z-index: 9999999 !important;
  background: #ffffff !important;
}

.debugtimeline.tl_slide .the_timeline_tester {
  background: #f39c12;
}

.debugtimeline.tl_frame .the_timeline_tester {
  background: #3498db;
}

.debugtimline_txt {
  color: #fff;
  font-weight: 400;
  font-size: 7px;
  position: absolute;
  left: 10px;
  top: 0px;
  white-space: nowrap;
  line-height: 10px;
}

.rtl {
  direction: rtl;
}

